import './Articlerigth.scss';
import article1img from '../../assets/article1.jpg';


const Articlerigth = ({ articles }) => {

    return(
        <div className="articleright">
            <div className="article__content">
                <h1 className="article__title">{articles[1].title}</h1>
                <a className="article__url" href={articles[1].url} target="_blank" rel='noreferrer noopener'>{articles[0].author}</a>
                <p className="article__text">{articles[1].description}</p>
                <a href={articles[1].url} target="_blank" rel='noreferrer noopener' className="article__keepreading">Continuar leyendo...</a>
            </div>
            <img className="article__img" alt={articles[1].title} src={article1img}/>
        </div>
    )
}

export default Articlerigth;

