import './Articleleft.scss';
import article0img from '../../assets/article0.jpg';


const Articleleft = ({ articles }) => {

    return(
        <div className="article" id="articleanchor">
            <div className="article__content">
                <h1 className="article__title">{articles[0].title}</h1>
                <a className="article__url" href={articles[0].url} target="_blank" rel='noreferrer noopener'>{articles[0].author}</a>
                <p className="article__text">{articles[0].description}</p>
                <a href={articles[0].url} target="_blank" rel='noreferrer noopener' className="article__keepreading">Continuar leyendo...</a>
            </div>
            <img className="article__img" alt={articles[0].title} src={article0img}/>
        </div>
    )
}

export default Articleleft;

